import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { OrderserviceService } from '../services/orderservice.service';
import { ActivatedRoute } from '@angular/router';
import { Order, orderDetail } from '../model';

@Component({
  selector: 'app-update',
  templateUrl: './update.component.html',
  styleUrls: ['./update.component.css']
})
export class UpdateComponent implements OnInit {

  constructor(private ordSvc: OrderserviceService, private fb:FormBuilder, private activatedRoute: ActivatedRoute) { }
  
  order: Order;
  ordDet: orderDetail;

  // like ngForm
  orderDetails: FormArray;
  form: FormGroup = this.createFrom({ email: '', orderDetails: []})


  ngOnInit() {
    const ordId = this.activatedRoute.snapshot.params.ordId;

    this.ordSvc.getOrder(ordId)
      .then(result => {
        this.order = result;
        //console.info("All orders: ", this.order);
        this.form = this.createFrom(result as Order);
        this.orderDetails = this.form.get('orderDetails') as FormArray
      })
      .catch(error => {
        console.info("error: ", error);
      })    
  }

  addOrderDetails() {
    console.info('before:', this.orderDetails.length);
    this.orderDetails.push(this.createOrderDetailsEmpty());
    console.info('after:', this.orderDetails.length);    
  }

  removeOrderDetails(idx: number) {
    console.info("the line is: ", idx);
    this.orderDetails.removeAt(idx);
  }

  updateOrder(){
    //console.info('values:', this.form.value);
    const order: Order ={
      ord_id: this.form.value['ord_id'],
      email: this.form.value['email'],
      orderDetails: []
    }
    
    for (let g =0; g < this.orderDetails.length; g++){
      const fg: FormGroup = this.orderDetails.controls[g] as FormGroup;
      order.orderDetails.push({
        description: fg.value['description'],
        quantity: parseInt(fg.value['quantity'] || 1)
      } as orderDetail)
    }
    
    console.info("Order:", order, " order.id is: ", order.ord_id);
    
    this.ordSvc.updateOrderDetails(order.ord_id.toString(),order)
      .then(()=> {
        console.info("record updated");
        this.orderDetails = this.fb.array([]);
        this.form.controls['orderDetails']=this.orderDetails;
        this.form.reset();
      })
      .catch(err => console.error('error: ', err));
  }
  
  private createOrderDetailsEmpty(): FormGroup {
    return(
      this.fb.group({
        description: this.fb.control('',[Validators.required]),
        quantity: this.fb.control('1',[Validators.required,Validators.min(1)])
      })
    )
  }  

  private createOrderDetails(od: orderDetail): FormGroup {
    return(
      this.fb.group({
        description: this.fb.control(od.description,[Validators.required]),
        quantity: this.fb.control(od.quantity,[Validators.required,Validators.min(1)])
      })
    )
  }

  private createFrom(order: Order): FormGroup {
    const ordDetails = this.fb.array([]);
    for (let od of order.orderDetails)
      ordDetails.push(this.createOrderDetails(od as orderDetail));
    const ordForm = this.fb.group({
      ord_id: order.ord_id,
      email: this.fb.control(order.email, [ Validators.required,Validators.email, Validators.minLength(5)]),
      orderDetails: ordDetails
    })
    return (ordForm)
  }
  
  
}
