import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Order, orderDetail } from '../model';

@Injectable({
  providedIn: 'root'
})
export class OrderserviceService {

  constructor(private httpClient: HttpClient) { }

  createOrder(order: Order): Promise<any>{
    return (
      this.httpClient.post<Order>('/api/order', order).toPromise()
    )
  }

  getOrder(ordId: string): Promise<any>{
    console.info("enter getOrder Service: ", ordId);
    return (
      this.httpClient.get<Order>('/api/order/'+ordId).toPromise()
    )
  }

  updateOrderDetails(ordId: string, order: Order): Promise<any>{
    console.info("enter updateOrderDetails: ", ordId);
    return (
      this.httpClient.post<Order>('/api/order/'+ordId, order).toPromise()
    )
  }
}
